import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useStaffLibraryBorrowList } from "../../../../Hooks/ApiHooks/StaffManager/staff-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import IssuedBookItem from "../../../../../MembersTab/staff/Mainbody/LibraryHead/LibraryFunctions/IssuedBooks/IssuedBookItem";
import style from "../../../../../MembersTab/student/Mainbody/StudentClass/StudentLibrary/BorrowdBooks/BorrowdBooks.module.css";
import styleIssue from "../../../../../MembersTab/staff/Mainbody/LibraryHead/LibraryFunctions/IssuedBooks/IssuedBooks.module.css";
const StaffBorrowBook = ({ onClose, sid }) => {
  const { t } = useTranslation();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView();
  const [search, setSearch] = useState("");

  const { staffLibraryBorrowList, staffLibraryBorrowListLoading } =
    useStaffLibraryBorrowList({
      data: {
        sid: sid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !sid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (staffLibraryBorrowList?.list?.length) {
      let obj = nextPage(staffLibraryBorrowList?.list?.length);
      setState(() => obj);
    }
  }, [staffLibraryBorrowList?.list?.length]);

  return (
    <div className={style.addbookcontainer}>
      <div className={style.addbooktop}>
        <h6>{t("borrowed_books")}</h6>
        <img src="/images/user_signup/close.svg" onClick={onClose} />
      </div>
      <div className={style.addbookbody}>
        <div
          className={style.membersContainer}
          style={{
            flexWrap: "wrap",
          }}
        >
          {staffLibraryBorrowList?.list?.map((book, index) =>
            staffLibraryBorrowList?.list?.length === index + 1 ? (
              <div
                key={book?._id}
                ref={ref}
                className={styleIssue.issuedBookItem}
                style={{
                  width: "49%",
                  marginBottom: "0.7rem",
                }}
              >
                <IssuedBookItem book={book} isStaff />
              </div>
            ) : (
              <div
                key={book?._id}
                className={styleIssue.issuedBookItem}
                style={{
                  width: "49%",
                  marginBottom: "0.7rem",
                }}
              >
                <IssuedBookItem book={book} isStaff />
              </div>
            )
          )}
        </div>

        {staffLibraryBorrowListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          staffLibraryBorrowList?.list?.length === 0 && (
            <EmptyMessage
              title="No item found related this search."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        ) : (
          staffLibraryBorrowList?.list?.length === 0 && (
            <EmptyMessage
              title="No item."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        )}
      </div>
    </div>
  );
};

export default StaffBorrowBook;
