import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useStaffLibraryHistoryList } from "../../../../Hooks/ApiHooks/StaffManager/staff-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../../../../MembersTab/student/Mainbody/StudentClass/StudentLibrary/BorrowdBooks/BorrowdBooks.module.css";
import CollectLogItem from "../../../../../MembersTab/staff/Mainbody/LibraryHead/LibraryFunctions/CollectedLogs/CollectLogItem";
import styleHistory from "../../../../../MembersTab/staff/Mainbody/LibraryHead/LibraryFunctions/CollectedLogs/CollectedLogs.module.css";

const StaffHistoryBook = ({ onClose, sid }) => {
  const { t } = useTranslation();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView();
  const [search, setSearch] = useState("");

  const { staffLibraryHistoryList, staffLibraryHistoryListLoading } =
    useStaffLibraryHistoryList({
      data: {
        sid: sid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !sid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (staffLibraryHistoryList?.list?.length) {
      let obj = nextPage(staffLibraryHistoryList?.list?.length);
      setState(() => obj);
    }
  }, [staffLibraryHistoryList?.list?.length]);

  return (
    <div className={style.addbookcontainer}>
      <div className={style.addbooktop}>
        <h6>{t("book_history")}</h6>
        <img src="/images/user_signup/close.svg" onClick={onClose} />
      </div>
      <div className={style.addbookbody}>
        <div
          className={style.membersContainer}
          style={{
            flexWrap: "wrap",
          }}
        >
          {staffLibraryHistoryList?.list?.map((book, index) =>
            staffLibraryHistoryList?.list?.length === index + 1 ? (
              <div
                key={book?._id}
                ref={ref}
                className={styleHistory.issuedBookItem}
                style={{
                  width: "49%",
                  marginBottom: "0.7rem",
                }}
              >
                <CollectLogItem book={book} isStaff />
              </div>
            ) : (
              <div
                key={book?._id}
                className={styleHistory.issuedBookItem}
                style={{
                  width: "49%",
                  marginBottom: "0.7rem",
                }}
              >
                <CollectLogItem key={book?._id} book={book} isStaff />
              </div>
            )
          )}
        </div>

        {staffLibraryHistoryListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          staffLibraryHistoryList?.list?.length === 0 && (
            <EmptyMessage
              title="No item found related this search."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        ) : (
          staffLibraryHistoryList?.list?.length === 0 && (
            <EmptyMessage
              title="No item."
              customStyleContainer={{
                marginTop: "1.5rem",
              }}
            />
          )
        )}
      </div>
    </div>
  );
};

export default StaffHistoryBook;
