import { useTranslation } from "react-i18next";
import { useLocation, useNavigate, useSearchParams } from "react-router-dom";
import { Suspense } from "react";
import UserMemberTabsWrapper from "../../../Ui/UserMember/UserMemberTabsWrapper";
import UserMemberMainWrapper from "../../../Layout/UserMemberTab/UserMemberMainWrapper";
import QvipleLoader from "../../../Loader/QvipleLoader";
import StaffLibraryProfile from "./StaffLibraryProfile";
import AllBooks from "../../../../MembersTab/student/Mainbody/StudentClass/StudentLibrary/AllBooks/AllBooks";
import BookDetails from "../../../../MembersTab/staff/Mainbody/LibraryHead/LibraryFunctions/AllBooks/BookDetails/BookDetails";
import StaffBorrowBook from "./Borrow/StaffBorrowBook";
import StaffHistoryBook from "./History/StaffHistoryBook";
const StaffLibrary = ({ lid, staffId, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const onClose = () => {
    navigate(-1);
  };
  return (
    <>
      <UserMemberMainWrapper>
        <Suspense fallback={<QvipleLoader />}>
          {!searchParams.get("type") ? (
            <UserMemberTabsWrapper>
              <StaffLibraryProfile />
            </UserMemberTabsWrapper>
          ) : searchParams.get("type") === "book" ? (
            <AllBooks
              lid={lid}
              onClose={onClose}
              carryParentState={getQuery.state}
              whichFlow="STAFF"
            />
          ) : searchParams.get("type") === "bookdetail" ? (
            <BookDetails desig="staff book details" sid={staffId} />
          ) : searchParams.get("type") === "borrow" ? (
            <StaffBorrowBook sid={staffId} onClose={onClose} />
          ) : searchParams.get("type") === "history" ? (
            <StaffHistoryBook sid={staffId} onClose={onClose} />
          ) : null}
        </Suspense>
      </UserMemberMainWrapper>
    </>
  );
};

export default StaffLibrary;
