import { useLocation, useParams } from "react-router-dom";
import SquareTabWrapper from "../../../Ui/Tab/Square/SquareTabWrapper";
import SquareTabCard from "../../../Ui/Tab/Square/SquareTabCard";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";

const StaffLibraryProfile = () => {
  const params = useParams();
  const getQuery = useLocation();
  let measure = document.getElementById("589nsftsd")?.clientWidth;
  let height = "9rem";
  let width = "9rem";
  if (measure) {
    let disvible =
      measure > 870 ? (measure > 1100 ? 6 : 5) : measure > 700 ? 4 : 3;
    let mi = measure > 870 ? 1 : 2;
    width = `${Math.floor((measure - mi - 31 * disvible - 1) / disvible)}px`;
    height = width;
  }

  return (
    <>
      <SquareTabWrapper dynamicId="589nsftsd">
        <SquareTabCard
          label="all_books"
          iconUrl={`${assestsUserFinanceUrl}/student.svg`}
          url={`/q/${params.username}/member?a=stafflibrary&type=book`}
          state={getQuery.state}
          isAuthorized
          isVisible={{
            all_books: true,
          }}
          authorizedKey="all_books"
          customStyleContainer={{
            height: height,
            width: width,
          }}
        />
        <SquareTabCard
          label="borrowed_books"
          iconUrl={`${assestsUserFinanceUrl}/student.svg`}
          url={`/q/${params.username}/member?a=stafflibrary&type=borrow`}
          state={getQuery.state}
          isAuthorized
          isVisible={{
            borrowed_books: true,
          }}
          authorizedKey="borrowed_books"
          customStyleContainer={{
            height: height,
            width: width,
          }}
        />

        <SquareTabCard
          label="history"
          iconUrl={`${assestsUserFinanceUrl}/student.svg`}
          url={`/q/${params.username}/member?a=stafflibrary&type=history`}
          state={getQuery.state}
          isAuthorized
          isVisible={{
            history: true,
          }}
          authorizedKey="history"
          customStyleContainer={{
            height: height,
            width: width,
          }}
        />
      </SquareTabWrapper>
    </>
  );
};

export default StaffLibraryProfile;
